.search-div{
    position: fixed;
    height: 100vh;
    left: 0;
    top: 0;
    width: 100%;
    background: rgba(0,0,0,.95);
    display: flex;
    flex-direction: column;
    gap: 20px;
    z-index: 4;
    overflow: auto;
}
.wt{
    color: #fff;
    font-size: 70px;
    cursor: pointer;
}
.search-bar-div{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    background: rgba(0,0,0,.8);
    gap: 20px;
}
.searchinput {
    width: 100%;
    padding: 16px;
    color: #fff;
    background: transparent;
    border: 1px solid rgba(255,255,255,.1);
    border-radius: 4px;
}
.searchResult{
    display: flex;
    justify-content: center;
    color: #fff;
    flex-direction: column;
    gap: 20px;
    background: rgba(0,0,0,.9);
    padding: 20px 20px 20px 40px;
}
.searchResult ul.moviesUl {
    background: rgba(255,255,255,.07);
    padding-top: 18px;
    margin-top: 21px;
    border-radius: 15px;
    padding-bottom: 18px;
}
.searchResult ul.moviesUl .movieName {
    font-size: 13px;
    background: rgba(0,0,0,.7);
}
.actor-photo img{
    width: 150px;
    height: 225px;
}
.actor-ul{
    list-style: none;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 10px;
}
.movies-section p,.people-section p, .company-section p {
    text-align: center;
    padding: 30px;
    font-size: 11px;
}
@media only screen and (max-width:600px){
    .searchResult {
        padding: 2px;
    }
}