li.movieImg img {
    width: 200px;
    cursor: pointer;
    border-radius: 5px;
}
.ReactModalPortal {
    position: relative;
    z-index: 20000;
}
.imgTitle {
    font-size: 3vw;
    padding-left: 47px;
    margin-top: 0px;
    color: #fff;
    font-size: 25px;
    display: flex;
    align-items: center;
    gap:10px;
}
.images {
    background: rgba(0,0,0,.6);
    padding-top: 35px;
}
ul.movie-photos {
    display: flex;
    gap: 10px;
    overflow: auto;
    padding: 50px;
    background: rgba(0,0,0,.9);
    margin: 0px 10px;
    border-radius: 9px;
    margin-left: 47px;
    margin-top: 0px;
    padding-top: 20px;
    padding-bottom: 0;
    padding-left: 5px;
    list-style-type:none;
}
.vidframe{
    width:400px;
    height: 340;
    border: 1px solid teal;
    padding: 25px;
    border-radius: 0;
    border-radius: 10px;
}
@media screen and (max-width:900px) {
    .imgTitle,ul.movie-photos,.similar_movies{
        padding-left: 15px !important;
    }
    ul.movie-photos{
        margin-left: 0px !important;
    }
}
@media only screen and (max-width: 600px){
    .imgTitle {
        padding-left: 10px !important;
        margin-bottom: 15px;
    }
    ul.movie-photos {
        padding: 20px;
        margin-left: 0px;
        padding-left: 0;
        padding-top: 0px;
        list-style-type:none;
    }
    .images {
        background: rgba(0,0,0,.6);
        padding: 15px;
        padding-top: 40px !important;
    }
    .imgTitle svg{
        color: rgb(16, 87, 87);
    }
    svg{
        color: rgb(16, 87, 87);
    }
    .vidframe{
        width:300px;
    }
}