ul.castlist {
    display: flex;
    overflow: auto;
    gap: 10px;
    list-style: none;
    margin-top: 20px;
    padding-left: 47px;
    min-height: 82px;
}
.cast-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
}
.castpic img {
    width: 50px;
    height: 50px;
    border-radius: 50%;
}
.cast-charecter {
    min-width: 160px;
    display: flex;
    flex-direction: column;
    gap: 4px;
}
.castpic {
    width: 70px;
    height: 70px;
    background: white;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    background-size: cover;
    background-repeat: no-repeat;
}

ul.castlist li {
    border: 1px solid
    rgba(255,255,255,.2);
    padding: 5px 30px 5px 5px;
    border-radius: 47px;
    background: rgba(0,0,0,.5);
}
.castTitle {
    font-size: 3vw;
    padding-left: 47px;
    margin-top: 30px;
    display: flex;
    align-items: center;
    gap: 10px;
}
p.charecter-name {
    font-size: 12px;
    color:teal;
}
@media screen and (max-width:900px) {
    ul.castlist{
        padding-left: 15px !important;
    }
}
@media only screen and (max-width:600px){
    ul.castlist{
        padding-left: 0px;
        margin-top: 0;
    }
    .castTitle{
        font-size: 6vw !important;
        padding-left: 10px !important;
        margin-bottom: 15px;
    }
    .castTitle svg{
        font-size: 7vw;
    }
}