.moviesUl{
    display: flex;
    list-style-type:none ;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    gap: 20px;
    padding: 0;
    margin: 0;
}
.movie {
    position: relative;
    transition: all .5s linear;
    cursor: pointer;
    
}
.movie:hover{
    /* transform: scale(1.3); */
    z-index: 3;
    border-radius: 15px;
}

.moviesUl li{
    display:inline-block;
}
.poster {
    width: 150px;
    border-radius: 17px;
    cursor: pointer;
    position: relative;
    z-index: 0;
}
.pagination {
    display: flex;
    background: rgba(0,0,0,.8);
    color: #fff;
    font-size: 50px;
    justify-content: space-between;
    padding: 0px 30px 0px 30px;
    margin-bottom: 80px;
    position: sticky;
    top: -1px;
    align-items: center;
    left: 0;
    z-index: 2;
}
svg.sicon {
    position: absolute;
    right: 7px;
}
.stext{
    font-size: 12px;
    color: rgba(255,255,255,.5)
}
.search-area {
    font-size: 20px;
    display: flex;
    align-items: center;
    width: 300px;
    height: 40px;
    border: 1px solid rgba(255,255,255,.2);
    position: relative;
    border-radius: 4px;
    cursor:text;
    padding-left: 10px;
}
.movieDesc {
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 100%;
    border-radius: 10px;
    justify-content: space-between;
   /* box-shadow: inset 0px 45px 94px 75px rgba(0,0,0,.99);*/
}
.pagination img{
    width:140px;
}
.prev,.next{
    cursor: pointer;
}

.movieName {
    display: flex;
    padding: 15px;
    background: rgba(0,0,0,.4);
    color: #fff;
    align-items: center;
    gap: 10px;
    font-size: 24px;
    border-radius: 15px 15px 0px 0px;
}
.actions {
    display: flex;
    padding: 15px;
    background: rgba(0,0,0,.3);
    color: #fff;
    align-items: center;
    gap: 10px;
    font-size: 27px;
    border-radius: 0px 0px 15px 15px;
    justify-content: flex-end;
    z-index: 1;
}


/*Movie Info*/
.movie_info {
    position: fixed;
    top: 0;
    left: 0;
    background: rgba(0,0,0,.85);
    width: 100%;
    height: 100vh;
    z-index: 10;
    overflow: auto;
}
.movie_backdrop{
    width:100%;
}
.poster_img{
    position: relative;
}
.movie_info_div {
    position: absolute;
    bottom: 0;
    left: 0;
    background-color: rgba(0,0,0,.76);
    color: #fff;
    padding: 0px;
    box-shadow: 0px -93px 102px 9px rgb(0 0 0 / 80%);
    width:100%;
}
.movieTitle {
    display: flex;
    align-items: center;
    gap: 10px;
    font-size: 30px;
    font-size: 6vw;
    padding-left: 47px;
}
.ic{
    font-size: 40px;
}
.goback{
    position: absolute;
    top:30px;
    right: 30px;
    z-index: 3;
    color: #fff;
    font-size: 50px;
    background: rgba(0,0,0,.4);
    border-radius: 50%;
    cursor: pointer;
    padding: 10px;
}
.gobackRel{
    color: #fff;
    font-size: 50px;
    cursor: pointer;
}
p.movie_desc {
    padding-left: 50px;
    margin-top: 12px;
    line-height: 1.5;
    font-size: 2vw;
}
.logo{
    width:130px;
    position: absolute;
    top:30px;
    left:30px;
    z-index: 4;
}
.similar_movies {
    padding: 52px;
    background: rgba(0,0,0,.6);
}
ul.similarMovieList {
    display: flex;
    gap: 15px;
    list-style: none;
    overflow: auto;
}
.the_backdrop img {
    width: 250px;
    border-radius: 10px;;
}
.smovie {
    position: relative;
}

.mtitle {
    color: #fff;
    position: absolute;
    bottom: 0;
    left: 0;
    padding: 15px 0px 15px 6px;
    background: rgba(0,0,0,.95);
    width: 100%;
    border-radius: 0px 0px 10px 10px;
}

.title{
    color:#fff;
    display: flex;
    align-items: center;
    gap: 10px;
    font-size: 25px;
    margin: 10px 0px 10px 0px;
}

span.genre-title {
    padding: 5px;
    background: rgba(255,255,255,.2);
    min-width: 87px;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 12px;
    border-radius: 20px;
    font-weight: 600;
    cursor: pointer;
}
.genre {
    padding-left: 50px;
    display: flex;
    gap: 10px;
}

@media only screen and (max-width: 1000px) {
    .movie_info_div {
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        background-color: rgba(0,0,0,.76);
        color: #fff;
        padding: 52px;
        box-shadow: 0px -93px 102px 9px rgb(0 0 0 / 80%);
        width: 100%;
        display: flex;
        justify-content: center;
        flex-direction: column;
    }
}


@media only screen and (max-width: 900px) {
    .movie_info_div,.movieTitle,.genre{
        padding: 15px;
    }
    p.movie_desc,.castTitle{
        padding-left: 15px !important;
    }
}
